/* 聊天区域 */

.lite-chatbox {
  height: calc(100% - 150px);
}


/* .lite-chatbox内的最后一条消息的margin-bottom设置为20px，离下面远一点 */

.lite-chatbox>div:last-child {
  margin-bottom: 20px;
}


/* 输入框 */


/* 缩小输入框内的图片，方便编辑 */

.lite-chatinput img {
  max-width: 150px;
  max-height: 150px;
  object-fit: contain;
}

.lite-chatinput {
  width: 100%;
  height: 150px;
  position: relative;
  bottom: 0px;
  background-color: #fff;
}


/* 聊天区域和输入框的分界线 */

.lite-chatinput .boundary {
  cursor: s-resize;
  margin: 0 auto;
  border-width: 1px 0px 0px 0px;
  border-color: rgba(0, 0, 0, 0.2);
  height: 5px;
  background: #fff;
}


/* 输入框 */

.lite-chatinput>.chatinput {
  position: relative;
  overflow-y: scroll;
  /* margin: 0px 3px 0px 3px; */
  width: calc(100% - 6px);
  margin: auto;
  /* width: 100%; */
  height: calc(100% - 75px);
  /* height: 100%; */
  border: none;
  outline: none;
  resize: none;
  font-size: 20px;
  word-break: break-all;
  overflow-wrap: break-word;
  padding: 5px;
  outline: none;
}


/* 发送按钮 */

.lite-chatinput .send {
  float: right;
  padding: 4px 20px 4px 20px;
  margin-right: 40px;
  color: white;
  background: -webkit-linear-gradient(70deg, #3FD1E1 0%, #44D7CD 100%);
  background: linear-gradient(20deg, #3f8fe1cc 0%, #44d7c9 100%);
  -webkit-box-shadow: 5px 5px 15px 0 rgba(102, 102, 102, 0.1);
  box-shadow: 5px 5px 15px 0 rgba(102, 102, 102, 0.1);
  border: none;
  border-radius: 6px;
  /* 老的 Firefox */
  -moz-border-radius: 6px;
  -webkit-border-radius: 6px;
}

.lite-chatinput .send:hover {
  background: -webkit-linear-gradient(70deg, #3FD1E1 0%, #44D7CD 60%);
  background: linear-gradient(20deg, #3f8fe1cc 0%, #44d7c9 60%);
}

.lite-chatinput .send:active {
  background: -webkit-linear-gradient(70deg, #3FD1E1 0%, #44D7CD 120%);
  background: linear-gradient(20deg, #3f8fe1cc 0%, #44d7c9 120%);
}


/* 输入框上方的输入表情等功能按钮 */

.lite-chatinput .tool-button {
  padding: 0px 3px 0px 3px;
  background: none;
  border: none;
  margin: 5px;
  margin-bottom: 0px;
}

.lite-chatinput .tool-button:hover {
  background: rgb(241, 241, 241);
}

.lite-chatinput .tool-button:active {
  background: rgb(230, 230, 230);
}

.lite-chatinput .tool-button path {
  fill: rgb(139, 135, 153);
}

.lite-chatinput .tool-button svg {
  width: 18px;
}


/* 由功能按钮唤起的功能页面 */

.lite-chatbox-tool {
  /* border: 5px solid red; */
  position: absolute;
  /* bottom: 20px; */
  margin-left: 3px;
  z-index: 3;
}


/* 部分功能页面需要用到的遮罩 */

#toolMusk {
  /* border: 3px solid red; */
  position: absolute;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  z-index: 2;
}

.float-left {
  float: left;
}

.float-right {
  float: right;
}


/* 滚动条 */


/* Firefox */

.lite-chatbox,
.lite-chatinput>.chatinput,
.scroll {
  scrollbar-width: thin;
  scrollbar-color: rgba(144, 147, 153, .3) rgba(144, 147, 153, .05);
}


/* else broswer */

::-webkit-scrollbar {
  /*滚动条整体样式*/
  width: 10px;
  /*高宽分别对应横竖滚动条的尺寸*/
  height: 1px;
}

::-webkit-scrollbar-thumb {
  /*滚动条里面小方块*/
  border-radius: 10px;
  background-color: rgba(144, 147, 153, .3);
  border: 0;
}

::-webkit-scrollbar-track {
  /*滚动条里面轨道*/
  background: #fff;
  min-height: 50%;
  min-height: 20px;
  border-radius: 8px;
}

::-webkit-scrollbar-corner {
  background-color: transparent;
}
